<template>
    <div class="container">
        <div class="newsTitle"> {{ this.actualArticle.name }}</div>
        <div class="newsDescription">
            <div class="newsImages">
                <div 
                :style="{backgroundImage: `url(${mainImage})`}"
                class="mainImage">
                </div>
                <div class="imageSelect">
                    <div 
                    v-bind:key="n"
                    v-for="n in 8"
                    @click="changeMainImage(n)"
                    :style="{backgroundImage: `url(${imagesPath}${n}.jpg)`}"
                    class="smallImg"></div>
                </div>
            </div>
            <div class="newsIntroduction"> {{ this.actualArticle.introduction }}</div>
            <div class="newsPivovar"> {{ this.actualArticle.pivovarText }}</div>
            <div class="newsBeer"> {{ this.actualArticle.beerText }}</div>
            <div class="newsConclusion"> {{ this.actualArticle.conclusion }}</div>
            <div class="finalRating">Celkový počet bodů: {{ this.avgRating() }} / 10</div>
        </div>
    </div>
</template>

<script>

// @ is an alias to /src
export default {
    name: "ArticleView",
    components: {},
    props:{
        articleID: Number
    },
    data() {
        return {
            actualArticle : this.$store.state.articles[this.$route.params.id-1],
            imagesPath: this.$store.state.articles[this.$route.params.id-1].pictures,
            mainImage : this.$store.state.articles[this.$route.params.id-1].pictures + '1.jpg',
            currentImage: 1,
        }
    },
    methods: {
        changeMainImage(num){
            this.mainImage = this.imagesPath + num + '.jpg';
        },
        avgRating() {
            let j = 0;
            this.$store.state.articles[this.$route.params.id-1].ratings.forEach(element => {
                j += element
            });
            return j / this.$store.state.articles[this.$route.params.id-1].ratings.length;
        }
    },
    computed: {

    }
};

</script>
<style scoped lang="scss">
@import "../../styles/globalstyles.scss";

.container{
    @include columnFlex;

    height:100%;
    margin-top:10px;
    gap:10px;

    .newsTitle{
        @include innerContainer;
        @include roundCorner;

        height:80px;
        background-color: $humanorange;

        align-items: center;
        justify-content: flex-start;

        font-weight: bold;
        font-size: 1.5em;
        color:white;
        text-transform: uppercase;

        box-sizing: border-box;
        padding:20px;
    }
    .newsDescription{
        @include innerContainer;
        @include roundCorner;
        
        text-align: justify;
        min-height: 70vh;
        background-color: $humanorange;
        color:white;
        
        flex-direction: column;
        gap:20px;
        box-sizing: border-box;
        padding:20px;
        .newsImages{
            @include roundCorner;
            width: 100%; height: 500px;
            background-color:$beerbrown;

            display: flex;
            justify-content: space-between;
            
            padding:10px;
            box-sizing: border-box;
            .mainImage{
                @include centerBG;
                width:70%;
                background-image: url('../../public/images/libocky/libocky1.jpg');
                // bug fix if img err 404 xddd
            }
            .imageSelect{
                width:29%;
                gap:10px;
                display:grid;
                grid-template-columns: 1fr 1fr;
                .smallImg{
                    height:100%;
                    background-size:cover;
                    background-position: center;
                }
                .smallImg:hover{
                    cursor:pointer;
                }
            }
        }
        .finalRating{
            width:100%; height: 80px;
            font-weight: bold;
            font-size: 2em;

            display: flex;
            justify-content: end;
            align-items: center;
        }
    }
}
</style>